.main__button {
  display: inline-block;
  text-align: center;
  background-color: #FFFFFF;
  border: 2px solid #FFFFFF;
  border-radius: 6px;
  padding: 12px 15px 8px;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
@media (min-width: 768px) {
  .main__button {
    padding: 17px 22px 13px;
  }
}
.main__button::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: #009EE3;
  top: 0;
  left: -100%;
  z-index: -1;
  transition: 0.3s;
}
.main__button:hover {
  border: 2px solid #009EE3;
  border-radius: 10px;
  position: relative;
  z-index: 100;
}
.main__button:hover::before {
  border: 1px solid #009EE3;
  left: 0;
}
.main__button:hover span {
  color: #FFFFFF;
  position: relative;
  z-index: 500;
  border-radius: 6px;
}
.button__text {
  font-family: "EuropeExt", "Arial", sens-serif;
  color: #009EE3;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 11px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .button__text {
    font-size: 17px;
